import React, { useState } from "react";
import { useColors } from "./ColorProvider.js";

export default function AddColor({ starsNum }) {
  const useInput = (initialValue) => {
    const [value, setValue] = useState(initialValue);
    return [
      { value, onChange: (e) => setValue(e.target.value) },
      () => setValue(initialValue),
    ];
  };

  const [colorProps, resetColor] = useInput("#A3A3A3");
  const [ratingProps, resetRating] = useInput(3);
  const { addColor } = useColors();

  const submit = (e) => {
    e.preventDefault();
    addColor(colorProps.value, ratingProps.value);
    resetColor();
    resetRating();
  };

  return (
    <form onSubmit={submit}>
      <input type="color" className="chooseColor" {...colorProps} required />
      <input
        className="ratingBox"
        type="number"
        min="1"
        max={starsNum}
        {...ratingProps}
        required
      />
      <button className="addColorButton">ADD</button>
    </form>
  );
}
