import React, { createContext, useState, useContext } from "react";
import useRealmApp from "./useRealmApp";

let collection,
  colorsData = null;

const ColorContext = createContext();
export const useColors = () => useContext(ColorContext);

export default function ColorProvider({ children }) {
  console.log("ColorProvider runs");
  const [colors, setColors] = useState([]);

  async function WrapColorQuery() {
    console.log("loging in to MongoDB");
    // collection = await useRealmApp();
    colorsData = await collection.find();
    setColors(colorsData);
  }
  if (!collection && !colorsData) {
    WrapColorQuery();
  }

  const dropColor = (id) => () => {
    let newColors = [...colors];
    newColors = colors.filter((entry) => entry._id !== id);
    setColors(newColors);
    try {
      collection.deleteOne({ _id: id });
    } catch (err) {
      if (err) throw err;
    }
  };

  const setRating = (id) => (rating) => {
    let newColors = colors.map((entry) =>
      entry._id === id ? { ...entry, rating: rating } : entry
    );
    setColors(newColors);
    const query = { _id: id };
    const newValue = { $set: { rating: rating } };
    collection.updateOne(query, newValue, (err, res) => {
      if (err) throw err;
      console.log(res);
    });
  };

  const addColor = (color, rating) => {
    async function insertWrapper() {
      await collection.insertOne({ color: color, rating: rating });
      const newColors = await collection.find();
      setColors(newColors);
    }
    try {
      insertWrapper();
    } catch (err) {
      if (err) throw err;
    }
  };

  return (
    <ColorContext.Provider value={{ colors, addColor, dropColor, setRating }}>
      {children}
    </ColorContext.Provider>
  );
}
