import React from "react";
import { BsFillTrashFill } from "react-icons/bs";
import StarRating from "./StarRating.js";

export default function ColorPanel({ color, rating, starsNum, dropColor, setRating }) {
  return (
    <div>
      <button>
        <BsFillTrashFill className="trash" onClick={dropColor} />
      </button>
      <div className="colorBlock" style={{ backgroundColor: color }}></div>
      <StarRating starsNum={starsNum} rating={rating} setRating={setRating}/>
      <p></p>
    </div>
  );
}
