import React from "react";
import { BsFillStarFill } from "react-icons/bs";

export default function StarRating({ starsNum, rating, setRating, style, props }) {
  const Star = ({ selected = false, onClick = (f) => f }) => (
    <BsFillStarFill color={selected ? "red" : "grey"} onClick={onClick} />
  );
  if (rating > starsNum) {
    rating = starsNum;
  }
  let starsArray = [];
  for (let i = 0; i < rating; i++) {
    starsArray.push(true);
  }
  for (let i = rating; i < starsNum; i++) {
    starsArray.push(false);
  }
  return (
    <div style={{ padding: "5px", ...style }} {...props}>
      {starsArray.map((selected, i) => (
        <Star key={i} selected={selected} onClick={() => setRating(i + 1)} />
      ))}
    </div>
  );
}
