import "./App.css";
import ColorPanel from "./components/ColorPanel.js";
import AddColor from "./components/AddColor.js";
import { useColors } from "./components/ColorProvider.js";

const starsNum = 5;
document.title = "star-rating";

export default function App() {
  const { colors, dropColor, setRating } = useColors();
  return (
    <>
      {colors.map((entry) => (
        <ColorPanel
          key={entry._id}
          color={entry.color}
          rating={entry.rating}
          starsNum={starsNum}
          dropColor={dropColor(entry._id)}
          setRating={setRating(entry._id)}
        />
      ))}
      <AddColor starsNum={starsNum} />
    </>
  );
}
